'use client'

import React, { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import { User, Mail, Sparkles, CreditCard, Save, Loader2 } from 'lucide-react'
import { createClient } from '@/lib/supabase/client'
import { Toaster, toast } from 'sonner' 

export default function SettingsPage() {
  const router = useRouter()
  const [user, setUser] = useState<any>(null)
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState(false)
  
  // 数据库里的真实数据
  const [profile, setProfile] = useState<any>(null)
  const [credits, setCredits] = useState(0)
  
  // 表单数据
  const [nickname, setNickname] = useState('')

  useEffect(() => {
    // 1. 检查登录状态
    const userStr = localStorage.getItem('currentUser')
    if (!userStr) {
      router.replace('/login')
      return
    }
    const localUser = JSON.parse(userStr)
    setUser(localUser)

    // 2. 从数据库拉取最新数据
    const fetchData = async () => {
      const supabase = createClient()
      const userId = localUser.id || localUser.sub

      if (supabase && userId) {
        // A. 获取个人资料 (昵称等)
        const { data: profileData } = await supabase
          .from('user_profiles')
          .select('*')
          .eq('user_id', userId)
          .single()
        
        if (profileData) {
          setProfile(profileData)
          setNickname(profileData.nickname || '')
        }

        // B. 获取积分
        const { data: creditData } = await supabase
          .from('user_credits')
          .select('credits')
          .eq('user_id', userId)
          .single()
        
        if (creditData) {
          setCredits(creditData.credits)
        }
      }
      setLoading(false)
    }

    fetchData()
  }, [router])

  // 保存修改
  const handleSave = async () => {
    if (!user) return
    setSaving(true)
    const supabase = createClient()
    const userId = user.id || user.sub

    try {
      // 更新数据库
      const { error } = await supabase!
        .from('user_profiles')
        .update({ nickname: nickname })
        .eq('user_id', userId)

      if (error) throw error

      // 更新本地缓存
      const newUser = { ...user, nickname, username: nickname }
      localStorage.setItem('currentUser', JSON.stringify(newUser))
      
      toast.success('保存成功！请刷新页面查看侧边栏变化') 
      window.location.reload()
      
    } catch (e) {
      console.error(e)
      alert('保存失败，请重试')
    } finally {
      setSaving(false)
    }
  }

  if (loading) {
    return (
      <div className="flex h-screen items-center justify-center bg-gray-50">
        <Loader2 className="h-8 w-8 animate-spin text-gray-400" />
      </div>
    )
  }

  return (
    <div className="flex flex-1 flex-col gap-4 p-4 md:p-8 bg-gray-50 min-h-screen">
      <Toaster position="top-center" />

      <div className="max-w-4xl mx-auto w-full space-y-8">
        
        <div>
          <h1 className="text-2xl font-bold tracking-tight text-gray-900">账号设置</h1>
          <p className="text-gray-500 mt-2">管理您的个人资料和订阅状态。</p>
        </div>

        <div className="grid gap-8 md:grid-cols-3">
          
          {/* 左侧：个人资料 */}
          <div className="md:col-span-2 space-y-6">
            <div className="bg-white rounded-xl border shadow-sm overflow-hidden">
              <div className="p-6 border-b bg-gray-50/50">
                <h2 className="font-semibold flex items-center gap-2">
                  <User className="w-4 h-4" /> 个人资料
                </h2>
              </div>
              <div className="p-6 space-y-4">
                {/* 头像 */}
                <div className="flex items-center gap-4">
                  <div className="h-16 w-16 rounded-full bg-blue-100 flex items-center justify-center text-blue-700 text-xl font-bold border-2 border-white shadow-sm overflow-hidden">
                     {user.photo ? (
                       <img src={user.photo} alt="avatar" className="w-full h-full object-cover" />
                     ) : (
                       (nickname || user.email || 'U').substring(0, 2).toUpperCase()
                     )}
                  </div>
                  <div>
                    <p className="font-medium text-gray-900">用户头像</p>
                    <p className="text-sm text-gray-500">头像同步自登录账号</p>
                  </div>
                </div>

                {/* 邮箱 */}
                <div className="space-y-2">
                  <label className="text-sm font-medium text-gray-700">注册邮箱</label>
                  <div className="flex items-center gap-2 p-2.5 bg-gray-100 rounded-md border text-gray-500">
                    <Mail className="w-4 h-4" />
                    <span>{user.email}</span>
                  </div>
                </div>

                {/* 昵称编辑 */}
                <div className="space-y-2">
                  <label className="text-sm font-medium text-gray-700">显示昵称</label>
                  <input 
                    type="text" 
                    value={nickname}
                    onChange={(e) => setNickname(e.target.value)}
                    placeholder="请输入您的昵称"
                    className="w-full p-2.5 rounded-md border focus:ring-2 focus:ring-black focus:border-black outline-none transition-all"
                  />
                </div>

                <div className="pt-2">
                  <button 
                    onClick={handleSave}
                    disabled={saving}
                    className="flex items-center gap-2 bg-black text-white px-4 py-2 rounded-md hover:bg-gray-800 transition-colors text-sm font-medium disabled:opacity-50"
                  >
                    {saving ? <Loader2 className="w-4 h-4 animate-spin" /> : <Save className="w-4 h-4" />}
                    {saving ? '保存中...' : '保存修改'}
                  </button>
                </div>
              </div>
            </div>
          </div>

          {/* 右侧：积分卡片 */}
          <div className="space-y-6">
            
            <div className="bg-white rounded-xl border shadow-sm p-6 relative overflow-hidden">
              <div className="absolute top-0 right-0 w-24 h-24 bg-yellow-100 rounded-full -mr-8 -mt-8 opacity-50 blur-xl"></div>
              <h3 className="text-sm font-medium text-gray-500 mb-2 flex items-center gap-2">
                <Sparkles className="w-4 h-4 text-yellow-500" /> 剩余积分
              </h3>
              <div className="text-3xl font-bold text-gray-900 mb-4">
                {credits} <span className="text-sm font-normal text-gray-500">分</span>
              </div>
              <p className="text-xs text-gray-400 mb-4">
                {/* ✨ 这里修改了文字说明 ✨ */}
                注册赠送 1000 积分，每次批改消耗 1 积分。
              </p>
              <button 
                onClick={() => router.push('/pricing')}
                className="w-full py-2 bg-yellow-500 hover:bg-yellow-600 text-white rounded-md text-sm font-medium transition-colors"
              >
                立即充值
              </button>
            </div>

            <div className="bg-white rounded-xl border shadow-sm p-6">
              <h3 className="text-sm font-medium text-gray-500 mb-2 flex items-center gap-2">
                <CreditCard className="w-4 h-4" /> 当前套餐
              </h3>
              <div className="flex items-center justify-between mb-4">
                <span className="text-lg font-semibold text-gray-900">免费版</span>
                <span className="px-2 py-1 bg-gray-100 text-gray-600 text-xs rounded-full">当前</span>
              </div>
              <button 
                onClick={() => router.push('/pricing')}
                className="w-full py-2 border border-gray-300 hover:bg-gray-50 text-gray-700 rounded-md text-sm font-medium transition-colors"
              >
                升级专业版
              </button>
            </div>

          </div>
        </div>
      </div>
    </div>
  )
}